* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.navbar, .goal {
  text-align: center;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 30px;
  font-weight: bolder;
}

.navbar {
  border-bottom: 2px solid #000;
}

.description {
  padding-top: 10px;
}

.loader {
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.loader:after {
  content: "";
  width: 20px;
  height: 20px;
  border: 5px solid #000;
  border-top-color: #add8e6;
  border-radius: 50%;
  animation: .75s infinite loading;
}

.loader-hidden {
  opacity: 0;
  visibility: hidden;
}

@keyframes loading {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(1turn);
  }
}

.puzzle-container {
  text-align: center;
  row-gap: 5px;
  display: grid;
}

.puzzle-row {
  outline-width: 0;
}

.letter {
  text-align: center;
  caret-color: #0000;
  color: #fff;
  height: 45px;
  width: 45px;
  border: 3px solid #d3d3d3;
  outline: none;
  margin: 5px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 30px;
  font-weight: bolder;
  display: inline-block;
}

.goal {
  text-align: center;
  padding-bottom: 30px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 15px;
  font-weight: bold;
}

.rules {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 15px;
  font-weight: bold;
  display: inline-block;
}

.legend {
  grid-template-rows: 1fr 1fr 1fr;
  justify-content: center;
  gap: 10px;
  margin: auto;
  display: grid;
}

.letter.red, .letter.green, .letter.yellow {
  color: #000;
  margin: 0;
  padding-left: 5px;
  padding-right: 5px;
  display: inline;
}

.letter.red {
  border-color: red;
}

.letter.green {
  background-color: #00a648;
}

.letter.yellow {
  background-color: #f7c652;
}

.row {
  display: inline-block;
}

.message {
  text-align: center;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 25px;
  font-weight: bold;
  display: inline-block;
  top: 50%;
  left: 50%;
}

